html,
body {
  height: 100%;
  width: 100%;
  padding: 0;
  margin: 0;
  background: #fafafa;
  font-family: 'Helvetica Neue', arial, sans-serif;
  font-weight: 400;
  color: #888;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

* {
  box-sizing: border-box;
}

.app {
  height: 400px;
  display: flex;
}

.game {
  display: flex;
  flex: 1;
  flex-direction: column;
  background-color: darkgreen;
}

.controls {
  display: flex;
  flex-direction: column;
  flex: 0.3;
  border-right: 1px solid black;
  background-color: #333;
  padding: 10px;
  align-items: center;
}

.control-button {
  width: 100%;
  font-size: 120%;
  font-weight: 700;
  margin: 10px;
  background: transparent;
  color: oldlace;
  border-radius: 4px;
}

.control-button:disabled {
  color: #666;
  border-color: #666;
}

.control-button:focus {
  color: #333;
  background-color: oldlace;
  outline: none;
}

.cards {
  display: flex;
  margin-left: 20px;
  margin-top: 20px;
}

.card {
  border-radius: 6px;
  border: 1px solid green;
  background-color: oldlace;
  width: 100px;
  height: 150px;
  text-align: center;
  font-size: 120%;
  font-weight: 700;
  font-variant: small-caps;
}

.red {
  color: red;
}

.black {
  color: #333;
}

.card-space {
  padding: 10px;
}
